:["poison"],"description":"Appears in filthy areas. Thrives by sucking up polluted sludge that is pumped out of factories.","hp":80,"attack":80,"defense":50,"spe_attack":40,"spe_defense":50,"speed":25,"number":"088"},{"id":89,"image":"dist/images/89.png","name":"Muk","types":["poison"],"description":"They love to gath er in smelly areas where sludge ac cumulates, making the stench around them worse.","hp":105,"attack":105,"defense":75,"spe_attack":65,"spe_defense":100,"speed":50,"number":"089"},{"id":90,"image":"dist/images/90.png","name":"Shellder","types":["water"],"description":"It swims facing backward by open ing and closing its two-piece shell. It is surprisingly fast.","hp":30,"attack":65,"defense":100,"spe_attack":45,"spe_defense":25,"speed":40,"number":"090"},{"id":91,"image":"dist/images/91.png","name":"Cloyster","types":["water","ice"],"description":"When attacked, it launches its horns in quick volleys. Its innards have never been seen.","hp":50,"attack":95,"defense":180,"spe_attack":85,"spe_defense":45,"speed":70,"number":"091"},{"id":92,"image":"dist/images/92.png","name":"Gastly","types":["poison","ghost"],"description":"This Pokmons body is 95% made up of gases, which are blown away by strong gusts of wind.","hp":30,"attack":35,"defense":30,"spe_attack":100,"spe_defense":35,"speed":80,"number":"092"},{"id":93,"image":"dist/images/93.png","name":"Haunter","types":["poison","ghost"],"description":"In total darkness, where nothing is visible, HAUNTER lurks, silently stalking its next victim.","hp":45,"attack":50,"defense":45,"spe_attack":115,"spe_defense":55,"speed":95,"number":"093"},{"id":94,"image":"dist/images/94.png","name":"Gengar","types":["poison","ghost"],"description":"The leer that floats in darkness belongs to a GENGAR delighting in casting curses on people.","hp":60,"attack":65,"defense":60,"spe_attack":130,"spe_defense":75,"speed":110,"number":"094"},{"id":95,"image":"dist/images/95.png","name":"Onix","types":["ground","rock"],"description":"As it digs through the ground, it absorbs many hard objects. This is what makes its body so solid.","hp":35,"attack":45,"defense":160,"spe_attack":30,"spe_defense":45,"speed":70,"number":"095"},{"id":96,"image":"dist/images/96.png","name":"Drowzee","types":["psychic"],"description":"Puts enemies to sleep then eats their dreams. Occasionally gets sick from eating bad dreams.","hp":60,"attack":48,"defense":45,"spe_attack":43,"spe_defense":90,"speed":42,"number":"096"},{"id":97,"image":"dist/images/97.png","name":"Hypno","types":["psychic"],"description":"HYPNO holds a pendulum in its hand. The arcing movement and glitter of the pendulum lull the foe into a deep state of hypnosis. While this POKMON searches for prey, it polishes the pendulum.","hp":85,"attack":73,"defense":70,"spe_attack":73,"spe_defense":115,"speed":67,"number":"097"},{"id":98,"image":"dist/images/98.png","name":"Krabby","types":["water"],"description":"KRABBY live on beaches, burrowed inside holes dug into the sand. On sandy beaches with little in the way of food, these POKMON can be seen squabbling with each other over territory.","hp":30,"attack":105,"defense":90,"spe_attack":25,"spe_defense":25,"speed":50,"number":"098"},{"id":99,"image":"dist/images/99.png","name":"Kingler","types":["water"],"description":"It can hardly lift its massive, overgrown pincer. The pincer's size makes it difficult to aim properly.","hp":55,"attack":130,"defense":115,"spe_attack":50,"spe_defense":50,"speed":75,"number":"099"},{"id":100,"image":"dist/images/100.png","name":"Voltorb","types":["electric"],"description":"It was discovered when POK BALLS were introduced. It is said that there is some connection.","hp":40,"attack":30,"defense":50,"spe_attack":55,"spe_defense":55,"speed":100,"number":"100"},{"id":101,"image":"dist/images/101.png","name":"Electrode","types":["electric"],"description":"It stores an over flowing amount of electric energy inside its body. Even a small shock makes it explode.","hp":60,"attack":50,"defense":70,"spe_attack":80,"spe_defense":80,"speed":140,"number":"101"},{"id":102,"image":"dist/images/102.png","name":"Exeggcute","types":["grass","psychic"],"description":"Often mistaken for eggs. When disturbed, they quickly gather and attack in swarms.","hp":60,"attack":40,"defense":80,"spe_attack":60,"spe_defense":45,"speed":40,"number":"102"},{"id":103,"image":"dist/images/103.png","name":"Exeggutor","types":["grass","psychic"],"description":"Legend has it that on rare occasions, one of its heads will drop off and continue on as an EXEGGCUTE.","hp":95,"attack":95,"defense":85,"spe_attack":125,"spe_defense":65,"speed":55,"number":"103"},{"id":104,"image":"dist/images/104.png","name":"Cubone","types":["ground"],"description":"Because it never removes its skull helmet, no one has ever seen this POKMON's real face.","hp":50,"attack":50,"defense":95,"spe_attack":40,"spe_defense":50,"speed":35,"number":"104"},{"id":105,"image":"dist/images/105.png","name":"Marowak","types":["ground"],"description":"The bone it holds is its key weapon. It throws the bone skillfully like a boomerang to KO targets.","hp":60,"attack":80,"defense":110,"spe_attack":50,"spe_defense":80,"speed":45,"number":"105"},{"id":106,"image":"dist/images/106.png","name":"Hitmonlee","types":["fighting"],"description":"Its legs freely stretch and contract. Using these springlike limbs, it bowls over foes with devastating kicks. After battle, it rubs down its tired legs.","hp":50,"attack":120,"defense":53,"spe_attack":35,"spe_defense":110,"speed":87,"number":"106"},{"id":107,"image":"dist/images/107.png","name":"Hitmonchan","types":["fighting"],"description":"Its punches slice the air. However, it seems to need a short break after fighting for three minutes.","hp":50,"attack":105,"defense":79,"spe_attack":35,"spe_defense":110,"speed":76,"number":"107"},{"id":108,"image":"dist/images/108.png","name":"Lickitung","types":["normal"],"description":"Its long tongue, slathered with a gooey saliva, sticks to anything, so it is very useful.","hp":90,"attack":55,"defense":75,"spe_attack":60,"spe_defense":75,"speed":30,"number":"108"},{"id":109,"image":"dist/images/109.png","name":"Koffing","types":["poison"],"description":"If KOFFING becomes agitated, it raises the toxicity of its internal gases and jets them out from all over its body. This POKMON may also overinflate its round body, then explode.","hp":40,"attack":65,"defense":95,"spe_attack":60,"spe_defense":45,"speed":35,"number":"109"},{"id":110,"image":"dist/images/110.png","name":"Weezing","types":["poison"],"description":"Where two kinds of poison gases meet, 2 KOFFINGs can fuse into a WEEZING over many years.","hp":65,"attack":90,"defense":120,"spe_attack":85,"spe_defense":70,"speed":60,"number":"110"},{"id":111,"image":"dist/images/111.png","name":"Rhyhorn","types":["ground","rock"],"description":"Strong, but not too bright, this POKMON can shatter even a skyscraper with its charging TACKLES.","hp":80,"attack":85,"defense":95,"spe_attack":30,"spe_defense":30,"speed":25,"number":"111"},{"id":112,"image":"dist/images/112.png","name":"Rhydon","types":["ground","rock"],"description":"Protected by an armor-like hide, it is capable of living in molten lava of 3,600 degrees.","hp":105,"attack":130,"defense":120,"spe_attack":45,"spe_defense":45,"speed":40,"number":"112"},{"id":113,"image":"dist/images/113.png","name":"Chansey","types":["normal"],"description":"A rare and elusive POKMON that is said to bring happiness to those who manage to get it.","hp":250,"attack":5,"defense":5,"spe_attack":35,"spe_defense":105,"speed":50,"number":"113"},{"id":114,"image":"dist/images/114.png","name":"Tangela","types":["grass"],"description":"It is shrouded by blue vines. No one has seen the face hidden behind this growth of vines.","hp":65,"attack":55,"defense":115,"spe_attack":100,"spe_defense":40,"speed":60,"number":"114"},{"id":115,"image":"dist/images/115.png","name":"Kangaskhan","types":["normal"],"description":"Raises its young in its belly pouch. Won't run from any fight to keep its young protected.","hp":105,"attack":95,"defense":80,"spe_attack":40,"spe_defense":80,"speed":90,"number":"115"},{"id":116,"image":"dist/images/116.png","name":"Horsea","types":["water"],"description":"If attacked by a larger enemy, it quickly swims to safety by adeptly controlling its well-developed dorsal fin.","hp":30,"attack":40,"defense":70,"spe_attack":70,"spe_defense":25,"speed":60,"number":"116"},{"id":117,"image":"dist/images/117.png","name":"Seadra","types":["water"],"description":"SEADRA sleeps after wriggling itself between the branches of coral. Those trying to harvest coral are occasionally stung by this POKMONs poison barbs if they fail to notice it.","hp":55,"attack":65,"defense":95,"spe_attack":95,"spe_defense":45,"speed":85,"number":"117"},{"id":118,"image":"dist/images/118.png","name":"Goldeen","types":["water"],"description":"Its tail fin billows like an elegant ballroom dress, giving it the nickname of the Water Queen.","hp":45,"attack":67,"defense":60,"spe_attack":35,"spe_defense":50,"speed":63,"number":"118"},{"id":119,"image":"dist/images/119.png","name":"Seaking","types":["water"],"description":"In the autumn, SEAKING males can be seen performing courtship dances in riverbeds to woo females. During this season, this POKMONs body coloration is at its most beautiful.","hp":80,"attack":92,"defense":65,"spe_attack":65,"spe_defense":80,"speed":68,"number":"119"},{"id":120,"image":"dist/images/120.png","name":"Staryu","types":["water"],"description":"As long as the center section is unharmed, it can grow back fully even if it is chopped to bits.","hp":30,"attack":45,"defense":55,"spe_attack":70,"spe_defense":55,"speed":85,"number":"120"},{"id":121,"image":"dist/images/121.png","name":"Starmie","types":["water","psychic"],"description":"At the center of its body is a red core, which sends mysterious radio signals into the night sky.","hp":60,"attack":75,"defense":85,"spe_attack":100,"spe_defense":85,"speed":115,"number":"121"},{"id":122,"image":"dist/images/122.png","name":"Mr-mime","types":["psychic"],"description":"It is adept at conning people. It is said to be able to create walls out of thin air by miming.","hp":40,"attack":45,"defense":65,"spe_attack":100,"spe_defense":120,"speed":90,"number":"122"},{"id":123,"image":"dist/images/123.png","name":"Scyther","types":["flying","bug"],"description":"With ninja-like agility and speed, it can create the illusion that there is more than one.","hp":70,"attack":110,"defense":80,"spe_attack":55,"spe_defense":80,"speed":105,"number":"123"},{"id":124,"image":"dist/images/124.png","name":"Jynx","types":["ice","psychic"],"description":"A JYNX sashays rhythmically as if it were dancing. Its motions are so bouncingly alluring, people seeing it are compelled to shake their hips without noticing.","hp":65,"attack":50,"defense":35,"spe_attack":115,"spe_defense":95,"speed":95,"number":"124"},{"id":125,"image":"dist/images/125.png","name":"Electabuzz","types":["electric"],"description":"Normally found near power plants, they can wander away and cause major blackouts in cities.","hp":65,"attack":83,"defense":57,"spe_attack":95,"spe_defense":85,"speed":105,"number":"125"},{"id":126,"image":"dist/images/126.png","name":"Magmar","types":["fire"],"description":"Its body always burns with an orange glow that enables it to hide perfectly among flames.","hp":65,"attack":95,"defense":57,"spe_attack":100,"spe_defense":85,"speed":93,"number":"126"},{"id":127,"image":"dist/images/127.png","name":"Pinsir","types":["bug"],"description":"PINSIR is astoundingly strong. It can grip a foe weighing twice its weight in its horns and easily lift it. This POKMONs movements turn sluggish in cold places.","hp":65,"attack":125,"defense":100,"spe_attack":55,"spe_defense":70,"speed":85,"number":"127"},{"id":128,"image":"dist/images/128.png","name":"Tauros","types":["normal"],"description":"These violent POKMON fight with other mem bers of their herd in order to prove their strength.","hp":75,"attack":100,"defense":95,"spe_attack":40,"spe_defense":70,"speed":110,"number":"128"},{"id":129,"image":"dist/images/129.png","name":"Magikarp","types":["water"],"description":"Famous for being very unreliable. It can be found swimming in seas, lakes, rivers and shallow puddles.","hp":20,"attack":10,"defense":55,"spe_attack":15,"spe_defense":20,"speed":80,"number":"129"},{"id":130,"image":"dist/images/130.png","name":"Gyarados","types":["flying","water"],"description":"It has an extremely aggressive nature. The HYPER BEAM it shoots from its mouth totally incinerates all targets.","hp":95,"attack":125,"defense":79,"spe_attack":60,"spe_defense":100,"speed":81,"number":"130"},{"id":131,"image":"dist/images/131.png","name":"Lapras","types":["water","ice"],"description":"A POKMON that has been over hunted almost to extinction. It can ferry people across the water.","hp":130,"attack":85,"defense":80,"spe_attack":85,"spe_defense":95,"speed":60,"number":"131"},{"id":132,"image":"dist/images/132.png","name":"Ditto","types":["normal"],"description":"A DITTO rearranges its cell structure to transform itself. However, if it tries to change based on its memory, it will get details wrong.","hp":48,"attack":48,"defense":48,"spe_attack":48,"spe_defense":48,"speed":48,"number":"132"},{"id":133,"image":"dist/images/133.png","name":"Eevee","types":["normal"],"description":"Its genetic code is irregular. It may mutate if it is exposed to radiation from element STONEs.","hp":55,"attack":55,"defense":50,"spe_attack":45,"spe_defense":65,"speed":55,"number":"133"},{"id":134,"image":"dist/images/134.png","name":"Vaporeon","types":["water"],"description":"When VAPOREON's fins begin to vi brate, it is a sign that rain will come within a few hours.","hp":130,"attack":65,"defense":60,"spe_attack":110,"spe_defense":95,"speed":65,"number":"134"},{"id":135,"image":"dist/images/135.png","name":"Jolteon","types":["electric"],"description":"It concentrates the weak electric charges emitted by its cells and launches wicked lightning bolts.","hp":65,"attack":65,"defense":60,"spe_attack":110,"spe_defense":95,"speed":130,"number":"135"},{"id":136,"image":"dist/images/136.png","name":"Flareon","types":["fire"],"description":"It has a flame chamber inside its body. It inhales, then blows out fire that is over 3,000F degrees.","hp":65,"attack":130,"defense":60,"spe_attack":95,"spe_defense":110,"speed":65,"number":"136"},{"id":137,"image":"dist/images/137.png","name":"Porygon","types":["normal"],"description":"A man-made Pokmon created using advanced scientific means. It can move freely in cyberspace.","hp":65,"attack":60,"defense":70,"spe_attack":85,"spe_defense":75,"speed":40,"number":"137"},{"id":138,"image":"dist/images/138.png","name":"Omanyte","types":["rock","water"],"description":"Although long extinct, in rare cases, it can be genetically regenerated from fossils.","hp":35,"attack":40,"defense":100,"spe_attack":90,"spe_defense":55,"speed":35,"number":"138"},{"id":139,"image":"dist/images/139.png","name":"Omastar","types":["rock","water"],"description":"A prehistoric POKMON that died out when its heavy shell made it impossible to catch prey.","hp":70,"attack":60,"defense":125,"spe_attack":115,"spe_defense":70,"speed":55,"number":"139"},{"id":140,"image":"dist/images/140.png","name":"Kabuto","types":["rock","water"],"description":"It is thought to have inhabited beaches 300 million years ago. It is protected by a stiff shell.","hp":30,"attack":80,"defense":90,"spe_attack":55,"spe_defense":45,"speed":55,"number":"140"},{"id":141,"image":"dist/images/141.png","name":"Kabutops","types":["rock","water"],"description":"KABUTOPS once swam underwater to hunt for prey. It was apparently evolving from being a water dweller to living on land as evident from changes in its gills and legs.","hp":60,"attack":115,"defense":105,"spe_attack":65,"spe_defense":70,"speed":80,"number":"141"},{"id":142,"image":"dist/images/142.png","name":"Aerodactyl","types":["flying","rock"],"description":"A savage POKMON that died out in ancient times. It was resurrected using DNA taken from amber.","hp":80,"attack":105,"defense":65,"spe_attack":60,"spe_defense":75,"speed":130,"number":"142"},{"id":143,"image":"dist/images/143.png","name":"Snorlax","types":["normal"],"description":"Its stomach can digest any kind of food, even if it happens to be moldy or rotten.","hp":160,"attack":110,"defense":65,"spe_attack":65,"spe_defense":110,"speed":30,"number":"143"},{"id":144,"image":"dist/images/144.png","name":"Articuno","types":["flying","ice"],"description":"The magnificent, seemingly translu cent wings of this legendary bird POKMON are said to be made of ice.","hp":90,"attack":85,"defense":100,"spe_attack":95,"spe_defense":125,"speed":85,"number":"144"},{"id":145,"image":"dist/images/145.png","name":"Zapdos","types":["flying","electric"],"description":"This legendary bird POKMON is said to appear only when a thun dercloud parts into two halves.","hp":90,"attack":90,"defense":85,"spe_attack":125,"spe_defense":90,"speed":100,"number":"145"},{"id":146,"image":"dist/images/146.png","name":"Moltres","types":["flying","fire"],"description":"One of the legendary bird POKMON. Those seeing it are overwhelmed by its orange wings that seem to be on fire.","hp":90,"attack":100,"defense":90,"spe_attack":125,"spe_defense":85,"speed":90,"number":"146"},{"id":147,"image":"dist/images/147.png","name":"Dratini","types":["dragon"],"description":"This POKMON is full of life ener gy. It continually sheds its skin and grows steadily larger.","hp":41,"attack":64,"defense":45,"spe_attack":50,"spe_defense":50,"speed":50,"number":"147"},{"id":148,"image":"dist/images/148.png","name":"Dragonair","types":["dragon"],"description":"A mystical POKMON that exudes a gentle aura. Has the ability to change climate conditions.","hp":61,"attack":84,"defense":65,"spe_attack":70,"spe_defense":70,"speed":70,"number":"148"},{"id":149,"image":"dist/images/149.png","name":"Dragonite","types":["flying","dragon"],"description":"It can fly in spite of its big and bulky physique. It circles the globe in just 16 hours.","hp":91,"attack":134,"defense":95,"spe_attack":100,"spe_defense":100,"speed":80,"number":"149"},{"id":150,"image":"dist/images/150.png","name":"Mewtwo","types":["psychic"],"description":"Because its battle abilities were raised to the ultimate level, it thinks only of de feating its foes.","hp":106,"attack":110,"defense":90,"spe_attack":154,"spe_defense":90,"speed":130,"number":"150"},{"id":151,"image":"dist/images/151.png","name":"Mew","types":["psychic"],"description":"Because its battle abilities were raised to the ultimate level, it thinks only of de feating its foes.","hp":100,"attack":100,"defense":100,"spe_attack":100,"spe_defense":100,"speed":100,"number":"151"}]
        }
    },
    methods: {

        getPokemons(endpoint){
            
            if ( $pokemons !== null ) return $pokemons;

            fetch(endpoint)
            .then((res) => {
                if (res.ok) return res.json(); 

                throw Error(res.statusText);
            })
            .catch(err => console.error(err))
            .then((pokemons) => {
                if (pokemons instanceof Array) {
                    
                    this.pokemons = pokemons;

                }
            });

        },

        
    },
    computed: {
	
        shorListed(){

            let list = $teamStore.getTeam();
    
            if( list.length === 0 ) return {};

            return this.pokemons.filter( p => list.includes(p.id) )

        }
    
       
    },

    created (){ 

       //this.pokemons = this.getPokemons(API_URL);

    }
}
</script>



